html {
    height: 100%;
    box-sizing: border-box;
  }
  *,
  *:before,
  *:after {
    box-sizing: inherit;
  }
  body {
    position: relative;
    margin: 0;
    padding-bottom: 6rem;
    min-height: 100%;
  }
 
.btn {
    padding: 10px;
    margin: 10px;
    color: rgb(62, 164, 33);
}
.back-btn button{
    color: rgb(102, 113, 102);
    width: 100%;
    padding: 20px;
    border-radius: 20px;
    
}
