ul {
    display: flex;
    justify-content: space-around;
    padding-top: 50px;
    /* background-color: aqua; */
    height: 100px;
    font-size: 2rem;
    color: white;
    flex-wrap: wrap;
}
li {
    list-style: none;
    
   
}
a {
    text-decoration: none;
    color: white;
}
a:hover{
    color: rgba(215, 186, 113, 0.31);
    transform: skew(30deg, 20deg);
}
a:active{
    color: goldenrod;
}

