/* Shop */
/* style={{display: "flex", flexWrap: "wrap",justifyContent:"space-around",alignItems:"center"}} */
.shop{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    align-items: center;
  background-color: #ccc;
  gap: 10px;
}
/* style={{display:"flex",flexDirection:"row",border:"1px solid",borderRadius:"10px",padding:"10px",width:"250px",justifyContent:"space-around",margin:"20px",alignItems:"center"}} */
.shop-cart{
    display: flex;
    flex-direction: row;
    border: 2px solid;
    border-radius: 10px;
    padding: 10px;
    width: 250px;
    justify-content: space-around;
    align-items: center;
    box-shadow: -5px 3px 10px 8px rgba(0,0,0,0.81)
    ;
}
 img {
    width: 200px;
}
img:hover {
    
    border-radius: 50%;
}

/* contact page css */
form{
    display:flex;
    flex-direction: column;
    width: 400px;
    gap: 20px;
    background-color: rgba(5, 12, 84, 0.413);
    margin: 0 auto;
    padding: 40px;
  

}
input {
    width: 200px;
    margin: 0 auto;
}
/* About style */
.about-sec{
    margin: 0 auto;
    background-color: rgba(84, 97, 84, 0.276);
    border: 1px solid;
    border-radius: 10px;
    padding: 10px;
    width: 500px;
}
/* Cart style */

.cart-div {
    background-color: rgb(163, 196, 196);
    box-shadow: 3px 4px 5px 3px rgb(174, 201, 174);
    display: flex;
    flex-direction: column;
    width: 250px;
    border: 2px solid;
    margin: 20px;
    padding: 10px;
    justify-content: space-around;
    border-radius: 10px;
   
    

}
.cart-item{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
   
}
.cart-div button {
    background-color: rgb(211, 47, 47);
    color: white;
    padding: 10px;
   
}
.cart-div button:hover {
    background-color: rgba(173, 216, 230, 0.166);
    color: black;
}
footer{
    position: absolute;
    right: 0;
    bottom: 0;
    left: 0;
    padding: 1rem;
    text-align: center;
    background-image: url("https://images.macrumors.com/t/jXqUxBjwyt16A254unbNN51zn9A=/1920x/https://images.macrumors.com/article-new/2019/02/MR-Future-Products-2020-2.png");
    background-size: cover;
    background-position: bottom;
    height: 100px;
    color: #fff;
    
   
}
